import React from 'react';
import { useDispatch } from 'react-redux';
import './header.css';
import UISlice from '../../slices/ui-slice'
import { useHistory } from 'react-router-dom';


const Header = () => {

    const dispatch = useDispatch();

    const history = useHistory();

    const showSettings = () => {
        dispatch(UISlice.actions.isShowSettings(true));
    }
    return (
            <nav class="navbar navbar-expand-md bg-dark navbar-dark" id="header">
                <a class="navbar-brand" href="#">Splinterlands Tracker</a>
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsibleNavbar">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <div class="collapse navbar-collapse" id="collapsibleNavbar">
                    <ul class="navbar-nav">
                        <li class="nav-item">
                            <a class="nav-link" href="/#/dashboard">DASHBOARD</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" href="/#/cards">CARDS</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" href="#/rented-cards">RENTED CARDS</a>
                        </li>
                        <li class="nav-item">
                            <div class="nav-link link" onClick={showSettings}>SETTINGS</div>
                        </li>
                    </ul>
                </div>
            </nav>
    )
}

export default Header;